import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'pic-dados',   templateUrl: 'pic_dados.component.html' }) export class PicDadosComponent implements OnInit {       displayMode: PicDadosDisplayModeEnum;     displayModeEnum = PicDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = PicDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = PicDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = PicDadosDisplayModeEnum.Edit;           break;       }     }  }  enum PicDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


